<template>
  <div class="layout-content flex1">
    <div class="main">
      <div class="contain df f13">
        <div class="contain-aside" :class="[isSlide ? 'contain-aside-hide' : '']">
          <div class="slide-main dict-menu">
            <p class="slide-title bb" style="box-sizing: content-box">数据字典</p>
            <ul>
              <li :class="[unitSet ? 'active' : '']" @click="unitTab">计量单位</li>
              <li :class="[responseSet ? 'active' : '']" @click="responseTab">响应时长设置</li>
            </ul>
          </div>
          <div class="slide-bar">
          <span class="slide-icon" @click="handleSlideToggle">
           <img src="../../../../../web-project/src/assets/icons/slide.png">
          </span>
          </div>
        </div>
        <div class="contain-main flex1">
          <div class="page-main">
            <div v-if='unitSet' class="mt10">
              <el-row>
                <el-col :span="16">
                  <el-button type="primary" @click="handleAddUnit">新增单位</el-button>
                </el-col>
              </el-row>
              <el-row class="mt20">
                <el-col :span="16">
                  <el-table
                    :data='tableData4'
                    border
                    :show-header=false
                    style='width: 100%'
                    @cell-mouse-enter="(row) => {handleHover(row, 'enter')}"
                    @cell-mouse-leave="(row) => {handleHover(row, 'leave')}">
                    <el-table-column
                      label='单位'>
                      <template slot-scope='scope'>
                        <el-row type="flex" justify="space-between">
                          <el-col class='ml10' :span="16">
                            {{scope.row.text}}
                          </el-col>
                          <el-col v-if="scope.row.show" :span="4">
                            <a class='no-decoration' href='javascript:void(0);'
                               @click='handleEditUnit(scope.row)'>编辑</a>
                            <a class='no-decoration' href='javascript:void(0);'
                               @click='handleDeleteUnit(scope.row)'>删除</a>
                          </el-col>
                        </el-row>
                      </template>
                    </el-table-column>
                  </el-table>
                </el-col>
              </el-row>
            </div>
            <div v-if='responseSet' class="mt10">
              <el-row class="mt20">
                <el-col :span="16">
                  <el-table
                    :data='tableData3'
                    border
                    style='width: 100%'>
                    <el-table-column
                      prop="text"
                      label='优先级'>
                    </el-table-column>
                    <el-table-column
                      label='响应时长（小时）'>
                      <template slot-scope='scope'>
                        <el-row>
                          <el-col class='ml10' :span="16">
                            <el-input
                              placeholder='24'
                              size="mini"
                              v-model='prioritySet[scope.row.index].res'>
                            </el-input>
                          </el-col>
                        </el-row>
                      </template>
                    </el-table-column>
                    <el-table-column
                      label='处理时长（小时）'>
                      <template slot-scope='scope'>
                        <el-row>
                          <el-col class='ml10' :span="16">
                            <el-input
                              placeholder='48'
                              size="mini"
                              v-model='prioritySet[scope.row.index].handle'>
                            </el-input>
                          </el-col>
                        </el-row>
                      </template>
                    </el-table-column>
                  </el-table>
                </el-col>
              </el-row>
              <el-row class="mt20">
                <el-col :span="16" style="text-align: center">
                  <el-button type="primary" @click="handleSavePriority">保 存</el-button>
                </el-col>
              </el-row>
            </div>
          </div>
        </div>
      </div>
    </div>
    <AddDialog @closeDialog="closeAddDialog" :dialogFormVisible="addDialogShow"></AddDialog>
    <EditDialog @closeDialog="closeEditDialog" :dialogFormVisible="editDialogShow"></EditDialog>
  </div>
</template>
<script>
  import AddDialog from './AddDialog.vue'
  import EditDialog from './EditDialog.vue'

  export default {
    components: {
      AddDialog, EditDialog
    },
    data() {
      return {
        isSlide: false,
        addDialogShow: false,
        responseSet: true,
        unitSet: false,
        editDialogShow: false,
        editKey: '',
        prioritySet: [{res: 24, handle: 48}, {res: 24, handle: 48}, {res: 24, handle: 48}, {res: 24, handle: 48}],
        tableData3: [
          {
            text: '重要紧急',
            index: 0
          },
          {
            text: '紧急',
            index: 1
          },
          {
            text: '一般',
            index: 2
          },
          {
            text: '不急',
            index: 3
          }
        ],
        tableData4: [
          {
            text: '辆',
            show: false,
          },
          {
            text: 'danwei',
            show: false
          },
          {
            text: '个',
            show: false
          }
        ]
      }
    },
    methods: {
      handleAddUnit() {
        this.addDialogShow = true
      },
      handleSavePriority() {
        console.log(this.prioritySet)
        // TODO api 提交响应修改
      },
      handleEditUnit(row) {
        this.editKey = row.key
        this.addDialogShow = true
      },
      handleDeleteUnit(row) {

      },
      closeAddDialog() {
        this.addDialogShow = false
      },
      closeEditDialog(form, method) {
        // TODO api 编辑
        this.editDialogShow = false
        this.editKey = ''
      },
      handleHover(row, event) {
        if (event === 'leave') {
          row.show = false
        }
        if (event === 'enter') {
          row.show = true
        }
      },
      unitTab() {
        this.unitSet = true
        this.responseSet = false
      },
      responseTab() {
        this.unitSet = false
        this.responseSet = true
      },
      handleSlideToggle() {
        this.isSlide = !this.isSlide
      }
    }
  };
</script>
<style lang="scss" src="./style.scss">

</style>


